     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 24px;
  background: #666666;
  /* Old browsers */
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.15)), to(#535353));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.15) 0%, #535353 100%);
  background: -o-linear-gradient(top, rgba(255, 255, 255, 0.15) 0%, #535353 100%);
