@charset "UTF-8";
.selectList {
  margin-left: -2px; }
  .selectList li {
    padding: 2px 2px; }
    .selectList li:not(:last-child) {
      border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
    .selectList li.hasAction {
      padding-right: 20px;
      position: relative; }
      .selectList li.hasAction .actionTrigger {
        position: absolute;
        right: 0;
        width: 16px;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        height: 100%;
        top: 0; }
        .selectList li.hasAction .actionTrigger:hover {
          color: #e44a4a; }
  .selectList.multi li {
    position: relative;
    cursor: pointer; }
    .selectList.multi li:hover {
      background: #5d5d5d; }
    .selectList.multi li.selected:after {
      content: '✔';
      position: absolute;
      right: 0;
      color: #252525; }
    .selectList.multi li:not(.selected) {
      color: #cccccc; }
  .selectList:not(.multi) li.selected {
    background: #252525;
    cursor: default;
    border-bottom-color: transparent; }
  .selectList:not(.multi) li:not(.selected) {
    color: #cccccc;
    cursor: pointer; }
    .selectList:not(.multi) li:not(.selected):hover {
      background: #5d5d5d; }
  .selectList.ellipsis li {
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis; }



